@import "../../assets/styles/app";

.roadmap {
  @include container;
  padding-top: 24px;

  @include desktop {
    padding-top: 56px;
  }

  .inner {
    @include inner;

    .title {
      @include h2;
    }

    .cards {
      margin-top: 26px;
      display: flex;
      flex-direction: column;
      align-items: center;

      .card {
        border: 6px solid rgba(0, 0, 0, 0.35);
        border-radius: 27px;
        overflow: hidden;
        width: 288px;
        height: 395px;
        display: flex;
        flex-direction: column;

        @include desktop {
          border: 6px solid rgba(0, 0, 0, 0.35);
          height: 358px;
          width: 100%;
          flex-direction: row;
        }

        &:not(:first-child) {
          margin-top: 20px;

          @include desktop {
            margin-top: 26px;
          }
        }

        &:nth-child(odd) {
          @include desktop {
            flex-direction: row;
          }
        }

        &:nth-child(even) {
          @include desktop {
            flex-direction: row-reverse;
          }
        }

        .gifWrapper {
          height: 200px;

          @include desktop {
            height: 100%;
            width: 688px;
            flex-shrink: 0;
          }
        }

        .texts {
          flex-grow: 1;
          padding-left: 20px;
          display: flex;
          flex-direction: column;
          justify-content: center;

          @include desktop {
            height: 100%;
            padding-left: 32px;
          }

          p {
            @include DigitalStrip2BB_Reg;
            font-weight: 700;
            font-size: 15px;
            line-height: 118%;
            letter-spacing: 0.04em;
            color: #000;

            @include desktop {
              font-size: 30px;
            }
          }

          .stages {
            margin-top: 24px;
          }
        }
      }


    }
  }
}