@import "../../assets/styles/app";

.texts {
  @include container;
  padding-top: 20px;

  @include desktop {
    padding-top: 30px;
  }

  .inner {
    @include inner;

    p {
      @include Itim;
      font-size: 14px;
      text-align: center;
      color: #000;

      @include desktop {
        font-size: 28px;
      }

      &:not(:first-child) {
        margin-top: 20px;

        @include desktop {
          margin-top: 40px;
        }
      }
    }
  }
}