@import "../../assets/styles/app";

.items {
  @include container;
  padding-top: 30px;

  @include desktop {
    padding-top: 40px;
  }

  .inner {
    @include inner;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(4, 1fr);
    grid-row-gap: 12px;

    @include desktop {
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(2, 1fr);
      grid-gap: 60px 24px;
    }

    .item {
      height: 143px;
      border: 3px solid rgba(0, 0, 0, 0.45);
      border-radius: 16px;
      padding: 20px 24px 0;

      @include desktop {
        height: 294px;
        border: 5px solid rgba(0, 0, 0, 0.45);
        border-radius: 32px;
        padding: 45px 50px 0;
      }

      .item_title {
        @include DigitalStrip2BB_Reg;
        font-weight: 700;
        font-size: 14px;
        line-height: 118%;
        letter-spacing: 0.04em;
        color: #000;

        @include desktop {
          font-size: 30px;
        }
      }

      .item_text {
        @include DigitalStrip2BB_Reg;
        font-weight: 700;
        font-size: 12px;
        line-height: 118%;
        letter-spacing: 0.04em;
        color: #000;
        margin-top: 12px;

        @include desktop {
          font-size: 25px;
          margin-top: 24px;
        }
      }
    }


  }
}