@import "../../assets/styles/app";

.launch {
  @include container;
  padding-top: 32px;

  @include desktop {
    padding-top: 82px;
  }

  .inner {
    @include inner;

    .title {
      @include h2;
      margin-left: auto;
      margin-right: auto;
    }

    .window {
      display: block;
      width: 100%;
      height: auto;
      margin-top: 32px;
      box-shadow: 0 10px 50px 0 rgba(0, 0, 0, 0.2), -10px 13px 16px 0 rgba(0, 0, 0, 0.3), 0 0 1px 0 rgba(0, 0, 0, 0.7);
      border-radius: 2px;

      @include desktop {
        margin-top: 60px;
        border-radius: 5px;
      }
    }
  }
}