@import "../../assets/styles/app";

.cards {
  padding-top: 40px;

  @include desktop {
    padding-top: 70px;
  }

  .wrapper {
    @include container;
    padding-left: 0;
    padding-right: 0;
    padding-top: 26px;
    padding-bottom: 26px;
    border-top: 7px solid rgba(0, 0, 0, 0.14);
    border-bottom: 7px solid rgba(0, 0, 0, 0.14);
    background: #afd650;

    @include desktop {
      padding-top: 36px;
      padding-bottom: 36px;
    }

    .cardsMobile {
      @include onlyMobile();

      .swiperSlide {
        width: 308px;
      }
    }

    .dots {
      @include onlyMobile(flex);
      justify-content: center;
      margin-top: 28px;

      .dot {
        @include button;
        width: 24px;
        height: 24px;
        border-radius: 50%;
        border: 3px solid #000;
        background: #FFF;
        transition: 0.3s;

        &:not(:first-child) {
          margin-left: 6px;
        }

        &_selected {
          background: linear-gradient(180deg, #30ee00 0%, #dbff00 100%);
        }

      }
    }

    .cardsDesktop {
      @include onlyDesktop(grid);
      @include inner;
      grid-template-columns: repeat(4, 1fr);
      grid-column-gap: 36px;
    }

    .card {
      position: relative;
      height: 493px;
      border: 6px solid rgba(0, 0, 0, 0.51);
      border-radius: 32px;
      overflow: hidden;
      padding-top: 16px;
      display: flex;
      flex-direction: column;
      align-items: center;

      .figure {
        position: absolute;
        z-index: 1;
        width: 100px;
        height: auto;
      }

      .mask {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        width: 100%;
        height: 142px;
        backdrop-filter: blur(8px);
        background: rgba(43, 43, 43, 0.65);
      }

      .card_title {
        @include FamilyGuy;
        @include border_black_5;
        position: relative;
        z-index: 3;
        font-weight: 400;
        font-size: 30px;
        letter-spacing: 0.04em;
        text-align: center;
        color: #fff;
      }

      .card_subtitle {
        @include DigitalStrip2BB_Bold;
        @include border_black_5;
        position: relative;
        z-index: 3;
        margin-top: 20px;
        font-weight: 700;
        font-size: 30px;
        letter-spacing: 0.04em;
        text-align: center;
        color: #fff;
        width: 235px;
      }

      &:nth-child(1) {
        .figure {
          position: absolute;
          top: -5px;
          left: -512px;
          width: 1280px;
        }
      }

      &:nth-child(2) {
        .figure {
          position: absolute;
          top: 5px;
          left: -512px;
          width: 1280px;
        }
      }

      &:nth-child(3) {
        .figure {
          position: absolute;
          top: -13px;
          left: -512px;
          width: 1194px;
        }
      }

      &:nth-child(4) {
        .figure {
          position: absolute;
          top: -60px;
          left: -257px;
          width: 754px;
        }
      }

      &:nth-child(1),
      &:nth-child(2),
      &:nth-child(3) {
        .card_subtitle {
          margin-top: 27px;
        }
      }
    }


  }


}