@import "../../assets/styles/app";

.whyFamilyPepe {
  @include container;
  padding-top: 25px;

  @include desktop {
    padding-top: 62px;
  }

  .inner {
    @include inner;
    height: 479px;
    position: relative;
    padding-top: 18px;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include desktop {
      height: 697px;
      padding-top: 40px;
    }

    .back {
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 30px;
      border: 6px solid rgba(0, 0, 0, 0.35);

      @include desktop {
        border-radius: 72px;
      }
    }

    .title {
      @include h2;
      position: relative;
      z-index: 10;



    }

    .text {
      position: relative;
      z-index: 10;
      margin-top: 43px;
      border: 1.5px solid rgba(0, 0, 0, 0.44);
      border-radius: 14px;
      width: 263px;
      padding: 16px;
      backdrop-filter: blur(7.599999904632568px);
      background: rgba(163, 195, 74, 0.65);

      @include desktop {
        margin-top: 94px;
        border: 3px solid rgba(0, 0, 0, 0.44);
        border-radius: 29px;
        width: 529px;
        padding: 16px;
      }

      p {
        @include DigitalStrip2BB_Reg;
        font-weight: 700;
        font-size: 14px;
        line-height: 118%;
        letter-spacing: 0.04em;
        text-align: center;
        color: #000;

        @include desktop {
          font-size: 27px;
        }
      }
    }

    .left0 {
      position: absolute;
      z-index: 1;
      width: auto;
      height: 149px;
      left: -22px;
      bottom: 34px;

      @include desktop {
        height: 378px;
        left: -65px;
        bottom: 0;
      }
    }

    .left1 {
      position: absolute;
      z-index: 2;
      width: auto;
      height: 180px;
      left: 48px;
      bottom: 70px;

      @include desktop {
        height: 456px;
        left: 110px;
        bottom: 106px;
      }
    }

    .center {
      position: absolute;
      z-index: 4;
      width: auto;
      height: 133px;
      left: calc(50% - 24px);
      transform: translateX(-50%);
      bottom: 0px;

      @include desktop {
        height: 338px;
        bottom: -45px;
        left: calc(50% - 0px) ;
      }
    }

    .right0 {
      position: absolute;
      z-index: 3;
      width: auto;
      height: 179px;
      right: 43px;
      bottom: 72px;

      @include desktop {
        height: 454px;
        right: 150px;
        bottom: 140px;
      }
    }

    .right1 {
      position: absolute;
      z-index: 1;
      width: auto;
      height: 150px;
      right: -16px;
      bottom: 20px;

      @include desktop {
        height: 382px;
        right: 0px;
        bottom: 10px;
      }
    }


  }
}