@import "../../assets/styles/app";

.footer {
  @include container;
  padding-top: 24px;
  padding-bottom: 16px;
  background: #847F69;

  @include desktop {
    padding-top: 58px;
    padding-bottom: 194px;
  }


  .socialLinks {
    display: flex;
    justify-content: center;

    .socialLink {
      @include centerRow;
      text-decoration: none;
      border-radius: 6px;
      box-shadow: 0 0 0 2px rgba(61, 112, 201, 0.4), inset 0 2px 1px 0 rgba(0, 0, 0, 0.25);
      background: #57761e;
      padding: 1px;

      @include desktop {
        padding: 2px 3px;
        border-radius: 14px;
      }

      &:not(:first-child) {
        margin-left: 6px;

        @include desktop {
          margin-left: 70px;
        }
      }

      .socialLink_wrapper {
        @include centerRow;
        border-radius: 5px;
        background: linear-gradient(180deg, #b0d44f 0%, #628f30 20.83%, #365914 94.27%);
        padding: 3px 4px;

        @include desktop {
          border-radius: 12px;
          padding: 7px 10px;
        }
      }

      .socialLink_inner {
        @include centerRow;
        border-radius: 3px;
        width: 78px;
        height: 22px;
        box-shadow: inset 0 1px 3px 0 rgba(255, 255, 255, 0.25);
        background: linear-gradient(180deg, #a1ca3d 0%, #5c8526 100%);

        @include desktop {
          border-radius: 8px;
          width: 197px;
          height: 56px;
        }

        span {
          @include FamilyGuy;
          @include border_darkgreen_1;
          font-weight: 400;
          font-size: 10px;
          letter-spacing: 0.04em;
          color: #faf5cd;

          @include desktop {
            font-size: 26px;
          }
        }
      }
    }
  }

  .address {
    margin-top: 12px;
    @include FamilyGuy;
    font-weight: 400;
    font-size: 15px;
    letter-spacing: 0.04em;
    text-align: center;
    background: linear-gradient(180deg, #30ee00 0%, #dbff00 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;

    @include desktop {
      margin-top: 32px;
      font-size: 30px;

    }
  }
}