@import "../../assets/styles/app";

.family {
  @include container;
  padding-top: 24px;

  @include desktop {
    padding-top: 16px;
  }

  .inner {
    @include inner;
    position: relative;
    height: 437px;
    border: 7px solid rgba(0, 0, 0, 0.42);
    border-radius: 30px;
    padding-top: 23px;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include desktop {
      height: 697px;
      border-radius: 72px;
      padding-top: 55px;
    }

    .back {
      position: absolute;
      display: block;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 30px;
      filter: blur(5.3px);

      @include desktop {
        border-radius: 72px;
      }
    }

    .left0 {
      position: absolute;
      z-index: 1;
      width: auto;
      height: 190px;
      left: -11px;
      bottom: 0;

      @include desktop {
        height: 420px;
        left: -30px;
      }
    }

    .left1 {
      position: absolute;
      z-index: 2;
      width: auto;
      height: 175px;
      left: 44px;
      bottom: 0;

      @include desktop {
        height: 386px;
        left: 118px;
      }
    }

    .left2 {
      position: absolute;
      z-index: 4;
      width: auto;
      height: 133px;
      left: 80px;
      bottom: -23px;

      @include desktop {
        height: 294px;
        left: 297px;
        bottom: -17px;
      }
    }

    .right0 {
      position: absolute;
      z-index: 3;
      width: auto;
      height: 123px;
      right: 73px;
      bottom: 0px;

      @include desktop {
        height: 272px;
        right: 270px;
      }
    }

    .right1 {
      position: absolute;
      z-index: 2;
      width: auto;
      height: 163px;
      right: 26px;
      bottom: 0px;

      @include desktop {
        height: 360px;
        right: 115px;
        bottom: 0px;
      }
    }

    .right2 {
      position: absolute;
      z-index: 1;
      width: auto;
      height: 178px;
      right: -20px;
      bottom: 5px;
      transform: rotateY(180deg);

      @include desktop {
        height: 394px;
        right: -31px;
        bottom: 16px;
      }
    }

    .title {
      position: relative;
      z-index: 10;
      @include FamilyGuy;
      @include border_black_2;
      font-weight: 400;
      font-size: 40px;
      letter-spacing: 0.03em;
      text-align: center;
      color: #30ee00;
      width: 280px;

      @include desktop {
        @include border_black_7;
        font-size: 156px;
        width: 659px;
      }
    }

    .subtitle {
      margin-top: 24px;
      @include Bangers;
      @include border_darkgreen_2;
      position: relative;
      z-index: 10;
      font-weight: 400;
      font-size: 38px;
      letter-spacing: 0.03em;
      text-align: center;
      color: #9ec44f;

      @include desktop {
        @include border_darkgreen_4;
        margin-top: 30px;
        font-size: 66px;
      }
    }

    .watchVideo {
      margin-top: 21px;
      position: relative;
      z-index: 10;
      @include centerRow;
      text-decoration: none;
      border-radius: 8px;
      box-shadow: 0 0 0 2px rgba(61, 112, 201, 0.4), inset 0 2px 1px 0 rgba(0, 0, 0, 0.25);
      background: #57761e;
      padding: 1px 2px;

      @include desktop {
        margin-top: 76px;
        border-radius: 14px;
        padding: 2px 3px;
      }

      .watchVideo_wrapper {
        @include centerRow;
        border-radius: 6px;
        background: linear-gradient(180deg, #b0d44f 0%, #628f30 20.83%, #365914 94.27%);
        padding: 4px 8px;

        @include desktop {
          border-radius: 12px;
          padding: 7px 10px;
        }
      }

      .watchVideo_inner {
        @include centerRow;
        border-radius: 4px;
        width: 147px;
        height: 30px;
        box-shadow: inset 0 1px 3px 0 rgba(255, 255, 255, 0.25);
        background: linear-gradient(180deg, #a1ca3d 0%, #5c8526 100%);

        @include desktop {
          border-radius: 8px;
          width: 274px;
          height: 56px;
        }

        span {
          @include FamilyGuy;
          @include border_darkgreen_1;
          font-weight: 400;
          font-size: 14px;
          letter-spacing: 0.04em;
          color: #faf5cd;

          @include desktop {
            font-size: 26px;
          }
        }
      }
    }

  }
}